import React, { Component } from "react";
import quotes from "./quotes";
//import QuoteBox from "./QuoteBox";

function QuoteBox(props) {
  console.log(props);
  let { quote, author, color } = props.state;
  return (
    <div id="quote-box">
      <div id="text">
        <span className="quote">" </span>
        {quote}
        <span className="quote"> "</span>
      </div>
      <div id="author">-{author}</div>
      <div className="buttons">
        <a href="twitter.com/intent/tweet" id="tweet-quote">
          <button
            className="btn btn-primary button"
            style={{ backgroundColor: color }}
          >
            Tweet
          </button>
        </a>
        <button
          className="btn btn-primary button"
          id="new-quote"
          onClick={props.newQuote}
          style={{ backgroundColor: color }}
        >
          New Quote
        </button>
      </div>
    </div>
  );
}

class App extends Component {
  constructor() {
    super();

    this.quotes = quotes;
    this.colors = [
      "#16a085",
      "#27ae60",
      "#2c3e50",
      "#f39c12",
      "#e74c3c",
      "#9b59b6",
      "#FB6964",
      "#342224",
      "#472E32",
      "#BDBB99",
      "#77B1A9",
      "#73A857"
    ];

    let initialIndex = this.getIndex(this.quotes);
    this.state = {
      quote: quotes[initialIndex].quote,
      author: quotes[initialIndex].author,
      color: this.colors[this.getIndex(this.colors)]
    };
  }

  getIndex = arr => {
    return Math.floor(Math.random() * arr.length);
  };

  handleClick = e => {
    let index = this.getIndex(this.quotes);
    this.setState({
      quote: quotes[index].quote,
      author: quotes[index].author,
      color: this.colors[this.getIndex(this.colors)]
    });
  };

  render() {
    console.log(quotes);
    console.log("test");
    return (
      <div className="app" style={{ backgroundColor: this.state.color }}>
        <QuoteBox state={this.state} newQuote={this.handleClick} />
      </div>
    );
  }
}

export default App;
